import http from '/api'

export const getworkarealist = (params = {}) =>
	http.fetchGet(`/workarea/list`, params)

export const deleteworkarea = (params = {}) =>
	http.fetchGet(`/workarea/delete`, params)

export const addworkarea = (params = {}) =>
	http.fetchPost(`/workarea/add`, params, true)

export const updateworkarea = (params = {}) =>
	http.fetchPost(`/workarea/update`, params, true)

export const getallworkareas = (params = {}) =>
	http.fetchPost(`/workarea/listAll`, params, true)


